import styled from "styled-components";
import { color } from "../../assets/colors";
import Prev from "../../assets/images/btns/btn__prev.png";
import Next from "../../assets/images/btns/btn__next.png";
import SwiperSlides from "../../components/SwiperSlides";
import { SwiperSlide } from "swiper/react";
import SwiperButton from "../../components/SwiperButton";
import { useArtist as data } from "../../hooks/useArtist";
import { useSwiper } from "../../hooks/useSwiper";

const options = {
    slidesPerView: 5,
    initialSlide: 2,
    pagination: {
        clickable: true,
    },
    observer: true,
    observeParents: true,
    centeredSlides: true,
};

const MainArtists = () => {
    const [onInit, onPrevBtn, onNextBtn] = useSwiper();
    return (
        <Block>
            <BlockContent>
                <h2>Artists</h2>
                <span>Discover Fablous Artists</span>
                <SwiperBox>
                    <SwiperButton sideImg={Prev} side="left" btn={onPrevBtn} />
                    <SwiperSlides options={options} onInit={onInit}>
                        {data.map((item) => (
                            <SwiperSlide key={item.id}>
                                <img src={item.img} alt={item.name} />
                                <h6>{item.name}</h6>
                                <span>{item.class}</span>
                            </SwiperSlide>
                        ))}
                    </SwiperSlides>
                    <SwiperButton sideImg={Next} side="right" btn={onNextBtn} />
                </SwiperBox>
            </BlockContent>
        </Block>
    );
};

export default MainArtists;

const Block = styled.div`
    width: 100%;
    padding: 74px 0 80px;
    background-color: ${color.white02};
`;
const BlockContent = styled.div`
    width: 100%;
    max-width: 1400px;
    margin: 0 auto;
    padding: 0 20px;
    box-sizing: border-box;
    text-align: center;
    h2 {
        font-size: 40px;
        color: ${color.black03};
        font-family: "Ms-SB";
        margin-bottom: 14px;
    }
    span {
        font-size: 18px;
        color: ${color.gray08};
        font-family: "Ms-R";
    }
`;
const SwiperBox = styled.div`
    position: relative;
    width: 100%;
    margin-top: 50px;
    padding: 0 117px;
    box-sizing: border-box;
    h6 {
        font-size: 16px;
        color: ${color.black02};
        font-family: "Ms-SB";
        margin: 14px 0 4px;
    }
    span {
        font-size: 14px;
        color: ${color.gray07};
        font-family: "Ms-R";
    }
    .swiper-wrapper {
        align-items: center;
        margin-left: -46px;
    }
    .swiper-slide {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 310px;
        height: 400px;
        img {
            width: 100%;
        }
        transform: scale(0.7);
        transition: transform 0.3s;
    }
    button {
        top: 45%;
        transform: translateY(-50%);
        min-width: 66px;
        height: 66px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
    }
    .swiper-slide-active {
        transform: scale(1.3);
        margin: 0 46px;
    }
`;
